import React, {useState, useEffect} from 'react'
import { Link } from 'react-router-dom'
import { Form, Button, Row, Col, FormLabel, FormControl } from 'react-bootstrap'
import {useDispatch, useSelector} from 'react-redux'
import Message from '../components/Message'
import Loader from '../components/Loader'
import FormContainer from '../components/FormContainer'
import { register } from '../actions/userActions'


const RegisterScreen = ({location, history}) => {

    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [confirmPassword, setConfirmPassword] = useState('')
    const [message, setMessage] = useState(null)

    const dispatch = useDispatch()

    const userRegister = useSelector(state => state.userRegister)
    const { loading, error, userInfo } = userRegister

    const redirect = location.search ? location.search.split('=')[1] : '/'

    useEffect(() => {
        if(userInfo) {
            history.push(redirect)
        }
    }, [history, userInfo, redirect])

    const submitHandler = (e) => {
        e.preventDefault()
        if(password !== confirmPassword){
            setMessage('Passwords do not Match')
        }else{
            dispatch(register(name,email,password))

        }

    }

    return (
        <FormContainer>
            <h1>Sign Up</h1>
            {message && <Message variant ='danger'>{message}</Message>}
            {error && <Message variant ='danger'>{error}</Message>}
            {loading && <Loader></Loader>}
            <Form onSubmit={submitHandler}>
            <Form.Group controlId='name'>
                    <FormLabel>Name </FormLabel>
                    <FormControl type='name' placeholder='Enter name' value={name} 
                    onChange={(e) => setName(e.target.value)}></FormControl>
                </Form.Group>
                <Form.Group controlId='email'>
                    <FormLabel>Email Address</FormLabel>
                    <FormControl type='email' placeholder='Enter Email' value={email} 
                    onChange={(e) => setEmail(e.target.value)}></FormControl>
                </Form.Group>
                <Form.Group controlId='password'>
                    <FormLabel>Password</FormLabel>
                    <FormControl type='password' placeholder='Enter password' value={password} 
                    onChange={(e) => setPassword(e.target.value)}></FormControl>
                </Form.Group>
                <Form.Group controlId='confirmPassword'>
          <Form.Label>Confirm Password</Form.Label>
          <Form.Control
            type='password'
            placeholder='Confirm password'
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
          ></Form.Control>
        </Form.Group>
                <Button type='Submit' variant='primary'>
                    Register
                </Button>
            </Form>
            <Row className='py-4'>
                <Col>
                    Have an Account?{' '}
                    <Link to={redirect ? `/login?redirect=${redirect}`:'/login'}>Register</Link>
                </Col>
            </Row>
        </FormContainer>
    )
}

export default RegisterScreen
